.video-card-grid .video-thumb {
  background-size: cover;
  position: relative;
  cursor: pointer;
}

.video-card-grid .video-thumb .thumb-overlay {
  width: 100%;
  height: 75%;
  position: absolute;
  top: 0;
  left: 0;
  border-top: 1.3em solid #fff;
  border-bottom: 1.3em solid #fff;
  outline-offset: -1px;
  outline: 2px solid #fff;
}

.video-card-grid .video-thumb .play-btn, .video-card-list .video-thumb .play-btn {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  z-index: 1;
}

.video-card-grid .video-meta {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.video-card-grid .video-meta .video-title {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.87);
  margin-bottom: 2px;
  cursor: pointer;
}

.video-card-grid .video-meta .video-channel, .video-card-grid .video-meta .video-views {
  font-size: 11px;
}

@media (min-width:750px) and (max-width: 959px) {

  .video-card-grid .video-thumb .thumb-overlay {
    border-top: 1.61em solid #fff;
    border-bottom: 1.61em solid #fff;
  }
}

@media (min-width:530px) and (max-width: 639px) {

  .video-card-grid .video-thumb .thumb-overlay {
    border-top: 1.61em solid #fff;
    border-bottom: 1.61em solid #fff;
  }
}



