.navbar {
  position: relative;
  min-height: 50px;
}

Header.app-header {
  -webkit-box-shadow: 0px 0px 5px 0 rgba(0,0,0,0.16), 0px 0px 0px 0 rgba(0, 0, 0, 0);
          box-shadow: 0px 0px 5px 0 rgba(0,0,0,0.16), 0px 0px 0px 0 rgba(0, 0, 0, 0);
  background: #fff;
  position: fixed;
  width: 100vw;
  z-index: 999;
}

.uk-logo > img {
  height:56px;
}

.anyaudio-search.uk-search-default {
  width: 100%;
}

.anyaudio-search.uk-search-default .uk-search-input {
  height: 56px;
  max-width: 687px;
  padding-left: 64px !important;
}

.border-radius40 {
  border-radius: 40px !important;
}

.anyaudio-search-container {
  -ms-flex-positive: 1;
      flex-grow: 1;
  padding-left: 20px;
}

.uk-search.anyaudio-search .uk-search-icon {
  left: 16px;
}

.uk-button.app-download-btn {
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(244, 67, 54, 0.87)), to(rgba(255, 167, 38, 0.87)));
  background-image: -webkit-linear-gradient(left, rgba(244, 67, 54, 0.87) 0%, rgba(255, 167, 38, 0.87) 100%);
  background-image: -o-linear-gradient(left, rgba(244, 67, 54, 0.87) 0%, rgba(255, 167, 38, 0.87) 100%);
  background-image: linear-gradient(90deg, rgba(244, 67, 54, 0.87) 0%, rgba(255, 167, 38, 0.87) 100%);
  border: 0;
  color: white;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  padding: 0 30px;
}

.uk-button.app-download-btn:hover {
  color: white;
  border: 0;
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(244, 67, 54, 0.71)), to(rgba(255, 167, 38, 0.71)));
  background-image: -webkit-linear-gradient(left, rgba(244, 67, 54, 0.71) 0%, rgba(255, 167, 38, 0.71) 100%);
  background-image: -o-linear-gradient(left, rgba(244, 67, 54, 0.71) 0%, rgba(255, 167, 38, 0.71) 100%);
  background-image: linear-gradient(90deg, rgba(244, 67, 54, 0.71) 0%, rgba(255, 167, 38, 0.71) 100%);
}

.app-header >.uk-container {
  margin-left: -16px;
  margin-right: 0;
  max-width: 100vw;

}

.navbar-custom .uk-navbar-item{
    height: 72px;
}

@media (max-width: 639px) {
  .app-header >.uk-container {
    padding-left: 0;
    padding-right: 0;
    margin-left: 0;
  }

  .anyaudio-search-container {
    padding-left: 30px;
    padding-right: 30px;
  }
}



